import React, { useState, useEffect } from 'react';
import InputMask from 'react-input-mask';
import './FormPage.scss';

import sendMessage from '../../../requests/sendMessage.js';
import formatFormData from '../../../helpers/formatFormData.js';

import { motion } from 'framer-motion';

export const FormPage = () => {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    phoneNumber: '',
    description: '',
  });

  const [errors, setErrors] = useState({});
  const [isFormValid, setIsFormValid] = useState(null);
  const [errorMessage, setErrorMessage] = useState(null);


  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });

    if (name === 'phoneNumber') {
      setFormData({
        ...formData,
        phoneNumber: value.replace(/\D/g, ''),
      });
    }
  };

  const validateForm = () => {
    const newErrors = {};

    if (!formData.firstName) {
      newErrors.firstName = '   ';
    }

    const phoneRegex = /^\d{9}$/;
    if (!phoneRegex.test(formData.phoneNumber)) {
      newErrors.phoneNumber = ' ';
    }

    setErrors(newErrors);

    return Object.keys(newErrors).length === 0;
  };

  const resetForm = () => {
    //       
    setFormData({
      firstName: '',
      lastName: '',
      phoneNumber: '',
      description: '',
    });
    setErrors({});
    setIsFormValid(null);
    setErrorMessage(null);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const isValid = validateForm();
    setIsFormValid(isValid);

    if (isValid) {
      const phoneNumberWithPrefix = `+380${formData.phoneNumber}`;
      const dataToSend = {
        ...formData,
        phoneNumber: phoneNumberWithPrefix,
      };

      console.log(',   :', dataToSend);
      window.location.href = '#/form-request';

      const message = formatFormData(dataToSend);

      try {
        const result = await sendMessage(message, { throwIfFailed: false });

        if (!result.ok) {
          // Request failed
          setErrorMessage('    .   .');
        } else {
          // Request OK
          //         
          resetForm(); //  
        }
      } catch (err) {
        // Do something with error...
        setErrorMessage('     .        .');
      }
    }
  };

  useEffect(() => {
    if (errorMessage) {
      //   ,    
      console.error(errorMessage);
    }
  }, [errorMessage]);

  return (
    <motion.div 
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      className="container"
    >
      <div className='form'>
        <h2 className='form__title'> </h2>
        <form onSubmit={handleSubmit}>
          <fieldset>
            <legend className='form-text'>  :</legend>
            <div className='form-container'>
              <div className="input-container__input">
                <input
                  type="text"
                  id="firstName"
                  name="firstName"
                  className="input-container__input--item"
                  value={formData.firstName}
                  onChange={handleChange}
                  required
                />
                {errors.firstName && (
                  <p className="input-message error">{errors.firstName}</p>
                )}
                {isFormValid !== null && !errors.firstName && (
                  <p className={`input-message ${isFormValid ? 'success' : 'error'}`}>
                    {isFormValid ? '' : ' '}
                  </p>
                )}
              </div>
              <div className="input-container__input">
                <input
                  type="text"
                  id="lastName"
                  name="lastName"
                  className="input-container__input--item"
                  value={formData.lastName}
                  onChange={handleChange}
                  required
                />
                {isFormValid !== null && !errors.lastName && (
                  <p className={`input-message ${isFormValid ? 'success' : 'error'}`}>
                    {isFormValid ? '' : ' '}
                  </p>
                )}
              </div>
            </div>
          </fieldset>
          <div className='form-telephone'>
            <label className='form-text' htmlFor="phoneNumber"> </label>
            <div className="input-container phone">
              <div className='number'>
                <div className="number-image"></div>
                <span>+380</span>
              </div>
              <InputMask
                placeholder=" "
                mask="999999999"
                maskChar=""
                value={formData.phoneNumber}
                onChange={handleChange}
              >
                {(inputProps) => (
                  <input
                    className='input-phone'
                    type="text"
                    id="phoneNumber"
                    name="phoneNumber"
                    {...inputProps}
                  />
                )}
              </InputMask>
            </div>
          </div>
          {errors.phoneNumber && (
            <p className="input-message error">{errors.phoneNumber}</p>
          )}
          {isFormValid !== null && !errors.phoneNumber && (
            <p className={`input-message ${isFormValid ? 'success' : 'error'}`}>
              {isFormValid ? '' : ' '}
            </p>
          )}
          <div>
            <label className='form-text' htmlFor="description"> </label>
            <div className="input-container">
              <textarea
                placeholder="    "
                className='input'
                id="description"
                name="description"
                value={formData.description}
                onChange={handleChange}
                required
              />
            </div>
            {isFormValid !== null && !errors.description && (
              <p className={`input-message ${isFormValid ? 'success' : 'error'}`}>
                {isFormValid ? '' : ' '}
              </p>
            )}
          </div>
          <button
            className="form--button" 
            type="submit" 
            id="submitButton"
          >
            
          </button>
        </form>
        {errorMessage && (
          <div className="error-message">
            {errorMessage}
          </div>
        )}
      </div>
    </motion.div>
  );
};
